import React, {Component} from 'react';
import { FaPlus } from 'react-icons/fa'

class AddPlayer extends Component {

    constructor() {
        super();
        this.state = {
            playername: '',
            ownerName: '',
            gameTime: '',
            nextGame: '',
            pNotes: ''
        };
        this.handleChange = this.handleChange.bind(this);
        this.handleAdd = this.handleAdd.bind(this);
    }
handleAdd(e) {
    e.preventDefault();
    let tempPlayer = {
        playername: this.state.playername,
        ownerName: this.state.ownerName,
        gameTime: this.state.gameTime + ' ' + this.state.nextGame,
        pNotes: this.state.pNotes
    };

    this.props.addPlayer(tempPlayer);

    this.setState({
        playername: '',
        ownerName: '',
        gameTime: '',
        nextGame: '',
        pNotes: ''
    });
    this.props.toggleForm();
}

handleChange(e) {
    const target = e.target;
    const value = target.value;
    const name = target.name;

    this.setState({
        [name]: value
    });
}
    render() {
        return (

        <div className={
            'card textcenter mt-3 ' +
            (this.props.formDisplay ? '': 'add-player')

        }
        >
        <div className="player-list-addheading card-header bg-primary text-white"
        onClick={this.props.toggleForm}
        >
          <FaPlus /> Add Player
        </div>

        <div className="card-body">
          <form id="playerForm" noValidate
          onSubmit={this.handleAdd}>
            <div className="form-group form-row">
              <label
                className="col-md-2 col-form-label text-md-right"
                htmlFor="playername"
                readOnly
              >
                Player Name
              </label>
              <div className="col-md-10">
                <input
                  type="text"
                  className="form-control"
                  name="playername"
                  placeholder="Player Name"
                  value={this.state.playername}
                  onChange={this.handleChange}
                />
              </div>
            </div>

            <div className="form-group form-row">
              <label
                className="col-md-2 col-form-label text-md-right"
                htmlFor="ownerName"
              >
                Team Owner
              </label>
              <div className="col-md-10">
                <input
                  type="text"
                  className="form-control"
                  name="ownerName"
                  placeholder="Owner's Name"
                  value={this.state.ownerName}
                  onChange={this.handleChange}
                />
              </div>
            </div>

            <div className="form-group form-row">
              <label
                className="col-md-2 col-form-label text-md-right"
                htmlFor="nextGame"
              >
                Date
              </label>
              <div className="col-md-4">
                <input
                  type="date"
                  className="form-control"
                  name="nextGame"
                  id="nextGame"
                />
              </div>
              <label
                className="col-md-2 col-form-label text-md-right"
                htmlFor="gameTime"
              >
                Game Time
              </label>
              <div className="col-md-4">
                <input
                  type="time"
                  className="form-control"
                  name="gameTime"
                  id="gameTime"
                  value={this.state.gameTime}
                  onChange={this.handleChange}
                />
              </div>
            </div>

            <div className="form-group form-row">
              <label className="col-md-2 text-md-right" htmlFor="pNotes">
                Player Notes
              </label>
              <div className="col-md-10">
                <textarea
                  className="form-control"
                  rows="4"
                  cols="50"
                  name="pNotes"
                  id="pNotes"
                  placeholder="Player Notes"
                  value={this.state.pNotes}
                  onChange={this.handleChange}
                />
              </div>
            </div>

            <div className="form-group form-row mb-0">
              <div className="offset-md-2 col-md-10">
                <button
                  type="submit"
                  className="btn btn-primary d-block ml-auto"
                >
                  Add Player
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default AddPlayer;