import React, {Component} from 'react';
import { GiBaseballGlove } from 'react-icons/gi';
import Moment from 'react-moment';

class ListPlayer extends Component {
    render() {
        return (
          
            <div className="player-list item-list mb-3">
                {this.props.playername.map( item => (
                    <div playername="player-item col media py-3" key={item.playerId}>
                <div className="mr-3">
                  <button className="player-delete btn btn-sm btn-danger"
                  onClick={() => this.props.deletePlayer(item)}>
                      <GiBaseballGlove />
                  </button>
                </div>
    
                <div className="player-info media-body">
                  <div className="player-head d-flex">
                    <span className="player-name" contentEditable
                    suppressContentEditableWarning
                    onBlur={
                        e => this.props.updateInfo('playername', e.target.innerText, item.playerId)
                    }
                    >
                    {item.playerId}. {item.playername}</span>
                    <span className="game-date ml-auto">
                    <Moment
                        date={item.nextGame}
                        parse= "YYYY-MM-dd hh:mm"
                        format = "MMM-DD h:mmp"
                        />
                    </span>
                  </div>
    
                  <div className="owner-name">
                    <span className="label-item">Owner: </span>
                    <span
                    contentEditable
                    suppressContentEditableWarning
                    onBlur={
                        e => this.props.updateInfo('ownerName', e.target.innerText, item.ownerId)
                    }
                    >
                    {item.ownerName}</span>
                  </div>
                  <div className="player-notes" contentEditable
                    suppressContentEditableWarning
                    onBlur={
                        e => this.props.updateInfo('pNotes', e.target.innerText, item.pNotesrId)
                    }>{item.pNotes}</div>
                </div>
              </div>
                ))}

              
            </div>  
        );
    }
}

export default ListPlayer;
