import React, { Component } from 'react';
//import logo from './orioles.png';
import '../css/App.css';
import AddPlayer from './AddPlayer'
import SearchPlayer from './SearchPlayer'
import ListPlayer from './ListPlayer'

import { findIndex, without } from 'lodash'

class App extends Component {
  constructor() {
    super();
    this.state = {
      playername: [],
      formDisplay: false,
      orderBy: 'playername',
      orderDir: 'asc',
      queryText: '',
      lastIndex: 1
    };
    this.deletePlayer = this.deletePlayer.bind(this);
    this.toggleForm = this.toggleForm.bind(this);
    this.addPlayer = this.addPlayer.bind(this);
    this.changeOrder = this.changeOrder.bind(this);
    this.SearchPlayer = this.SearchPlayer.bind(this);
    this.updateInfo = this.updateInfo.bind(this);
  }

  toggleForm() {
    this.setState({
      formDisplay: !this.state.formDisplay
    });
  }

SearchPlayer(query) {
  this.setState({queryText: query});
}

changeOrder(order, dir) {
    this.setState({
      orderBy: order,
      orderDir: dir
    });
  }

updateInfo(name, value, id) {
  let tempPlayer = this.state.playername;
  let playerIndex = findIndex(this.state.playername, {
    playernameId: id 
  });
  tempPlayer[playerIndex][name] = name;
  this.setState({
    playername: tempPlayer
  });
}


addPlayer(player) {
  let tempPlayer = this.state.playername;
  player.playerId = this.state.lastIndex;
  tempPlayer.unshift(player);
  this.setState({
    playerName: tempPlayer,
    lastIndex: this.state.lastIndex + 1 
  });
}

  deletePlayer(player) {
    let tempPlayer = this.state.playername;
    tempPlayer = without(tempPlayer, player);

    this.setState({
      playername: tempPlayer
    })
  }

  componentDidMount() {
    fetch('./data.json')
      .then(response => response.json())
      .then(result => {
        const playername = result.map(item => {
          item.playerId = this.state.lastIndex;
          this.setState({ lastIndex: this.state.lastIndex + 1});
          return item;
        });
        this.setState({
          playername: playername
        });
      });
  }

  render() {
    
    let order;
    let filteredplayername = this.state.playername;
    if (this.state.orderDir === 'asc') {
      order = 1;
    } else {
      order = -1;
    }

    filteredplayername = filteredplayername
      .sort((a, b) => {
        if (
          a[this.state.orderBy].toLowerCase() <
          b[this.state.orderBy].toLowerCase()
        ) {
          return -1 * order;
        } else {
          return 1 * order;
        }
      })
      .filter(eachItem => {
        return (
          eachItem['playername']
            .toLowerCase()
            .includes(this.state.queryText.toLowerCase()) ||
          eachItem['ownerName']
            .toLowerCase()
            .includes(this.state.queryText.toLowerCase()) ||
          eachItem['pNotes']
            .toLowerCase()
            .includes(this.state.queryText.toLowerCase())
        );
      });

    return (
      <main class="page bg-white" id="playerratings">
        <div class="container">
          <div class="row">
            <div class="col-md-12 bg-white">
              <div class="container">
                <AddPlayer 
                formDisplay={this.state.formDisplay}
                toggleForm={this.toggleForm}
                addPlayer = {this.addPlayer}
                />
			          <SearchPlayer
                orderBy={this.state.orderBy}
                orderDir={this.state.orderDir}
                changeOrder={this.changeOrder} 
                SearchPlayer={this.SearchPlayer}
                />
                <ListPlayer 
                  playername={filteredplayername}
                  deletePlayer={this.deletePlayer} 
                  updateInfo={this.updateInfo}
                  />
            </div>
          </div>
        </div>
      </div>
    </main>
    );
  }  
}

export default App;
